import React from "react";
import "./App.css";
import { TemTemTab } from "./TemTemTab";

function App() {
  const [text, setText] = React.useState<string>("");

  const handleSearchTextChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setText(e.currentTarget.value);
  };

  return (
    <div className="App">
      <h2>TemTem Type Search</h2>
      <input
        type="text"
        onChange={handleSearchTextChange}
        placeholder="Enter a TemTem name..."
      />
      <br />
      <br />
      <TemTemTab text={text} />
    </div>
  );
}

export default App;
