import React from "react";
import { TemTem, temTems as allTemTems, TemTemTypeReaction } from "./TemTem";
import { getIconFromType } from "./AppIcons";

interface TabProps {
  text: string;
}

export const TemTemTab = ({ text }: TabProps) => {
  if (text == null || text === "") return <div />;
  // Finds the matching TemTems //
  var temTems = allTemTems.filter((temTem) =>
    temTem.name.toUpperCase().includes(text.toUpperCase())
  );
  // Returns a list for each //
  return (
    <div>
      <p>
        {temTems.map((t) => (
          <TemTemSingleTab temTem={t} />
        ))}
      </p>
    </div>
  );
};

interface SingleTabProps {
  temTem: TemTem;
}

export const TemTemSingleTab = ({ temTem }: SingleTabProps) => (
  <div>
    <h3
      style={{ backgroundColor: "#303030", color: "white", padding: "7.5px" }}
    >
      {temTem.name}
      {getIconFromType(temTem.types[0])}
      {temTem.types.length === 2 ? getIconFromType(temTem.types[1]) : ""}
    </h3>
    <div>
      <div>
        Resistant to:
        {Array.from(temTem.reactions.entries()).map((entry) => {
          if (entry[1] === TemTemTypeReaction.RESISTANT_2X) {
            return getIconFromType(entry[0]);
          } else if (entry[1] === TemTemTypeReaction.RESISTANT_4X) {
            return getIconFromType(entry[0], true);
          }
        })}
      </div>
      <div>
        Weak to:
        {Array.from(temTem.reactions.entries()).map((entry) => {
          if (entry[1] === TemTemTypeReaction.WEAKNESS_2X) {
            return getIconFromType(entry[0]);
          } else if (entry[1] === TemTemTypeReaction.WEAKNESS_4X) {
            return getIconFromType(entry[0], true);
          }
        })}
      </div>
      <div></div>
    </div>
  </div>
);
