import React from "react";
import crystal from "./icons/crystal.png";
import digital from "./icons/digital.png";
import earth from "./icons/earth.png";
import electric from "./icons/electric.png";
import fire from "./icons/fire.png";
import melee from "./icons/melee.png";
import mental from "./icons/mental.png";
import nature from "./icons/nature.png";
import neutral from "./icons/neutral.png";
import toxic from "./icons/toxic.png";
import water from "./icons/water.png";
import wind from "./icons/wind.png";
import { TemTemType } from "./TemTem";

const CrystalIcon = <img src={crystal} title="Crystal" width="24px" />;
const DigitalIcon = <img src={digital} title="Digital" width="24px" />;
const EarthIcon = <img src={earth} title="Earth" width="24px" />;
const ElectricIcon = <img src={electric} title="Electric" width="24px" />;
const FireIcon = <img src={fire} title="Fire" width="24px" />;
const MeleeIcon = <img src={melee} title="Melee" width="24px" />;
const MentalIcon = <img src={mental} title="Mental" width="24px" />;
const NatureIcon = <img src={nature} title="Nature" width="24px" />;
const NeutralIcon = <img src={neutral} title="Neutral" width="24px" />;
const ToxicIcon = <img src={toxic} title="Toxic" width="24px" />;
const WaterIcon = <img src={water} title="Water" width="24px" />;
const WindIcon = <img src={wind} title="Wind" width="24px" />;

export function getIconFromType(
  temTemType: TemTemType,
  double: Boolean = false
): JSX.Element {
  switch (temTemType) {
    case TemTemType.CRYSTAL:
      return getIcon(CrystalIcon, double);

    case TemTemType.DIGITAL:
      return getIcon(DigitalIcon, double);

    case TemTemType.EARTH:
      return getIcon(EarthIcon, double);

    case TemTemType.ELECTRIC:
      return getIcon(ElectricIcon, double);

    case TemTemType.FIRE:
      return getIcon(FireIcon, double);

    case TemTemType.MELEE:
      return getIcon(MeleeIcon, double);

    case TemTemType.MENTAL:
      return getIcon(MentalIcon, double);

    case TemTemType.NATURE:
      return getIcon(NatureIcon, double);

    case TemTemType.TOXIC:
      return getIcon(ToxicIcon, double);

    case TemTemType.WATER:
      return getIcon(WaterIcon, double);

    case TemTemType.WIND:
      return getIcon(WindIcon, double);

    default:
      return getIcon(NeutralIcon, double);
  }
}

function getIcon(icon: JSX.Element, double: Boolean): JSX.Element {
  if (double) {
    return (
      <span>
        {icon}
        {icon}
      </span>
    );
  } else {
    return icon;
  }
}
