export enum TemTemType {
  NEUTRAL,
  WIND,
  EARTH,
  WATER,
  FIRE,
  NATURE,
  ELECTRIC,
  MENTAL,
  DIGITAL,
  MELEE,
  CRYSTAL,
  TOXIC,
}

export enum TemTemTypeReaction {
  WEAKNESS_4X,
  WEAKNESS_2X,
  NEUTRAL,
  RESISTANT_2X,
  RESISTANT_4X,
}

export class TemTem {
  name: string;
  index: number;
  types: TemTemType[];
  reactions: Map<TemTemType, TemTemTypeReaction>;

  constructor(name: string, index: number, types: TemTemType[]) {
    this.name = name;
    this.index = index;
    this.types = types;
    this.reactions = this.getReactions(types);
  }

  private getReactions(
    temTemTypes: TemTemType[]
  ): Map<TemTemType, TemTemTypeReaction> {
    var intMap = new Map<TemTemType, number>([
      [TemTemType.NEUTRAL, 0],
      [TemTemType.WIND, 0],
      [TemTemType.EARTH, 0],
      [TemTemType.WATER, 0],
      [TemTemType.FIRE, 0],
      [TemTemType.NATURE, 0],
      [TemTemType.ELECTRIC, 0],
      [TemTemType.MENTAL, 0],
      [TemTemType.DIGITAL, 0],
      [TemTemType.MELEE, 0],
      [TemTemType.CRYSTAL, 0],
      [TemTemType.TOXIC, 0],
    ]);
    this.getReactionIndexesFromType(intMap, temTemTypes[0]);
    if (temTemTypes.length === 2)
      this.getReactionIndexesFromType(intMap, temTemTypes[1]);

    var map = new Map<TemTemType, TemTemTypeReaction>();
    map.set(
      TemTemType.NEUTRAL,
      this.reactionFromNumber(intMap.get(TemTemType.NEUTRAL)!!)
    );
    map.set(
      TemTemType.WIND,
      this.reactionFromNumber(intMap.get(TemTemType.WIND)!!)
    );
    map.set(
      TemTemType.EARTH,
      this.reactionFromNumber(intMap.get(TemTemType.EARTH)!!)
    );
    map.set(
      TemTemType.WATER,
      this.reactionFromNumber(intMap.get(TemTemType.WATER)!!)
    );
    map.set(
      TemTemType.FIRE,
      this.reactionFromNumber(intMap.get(TemTemType.FIRE)!!)
    );
    map.set(
      TemTemType.NATURE,
      this.reactionFromNumber(intMap.get(TemTemType.NATURE)!!)
    );
    map.set(
      TemTemType.ELECTRIC,
      this.reactionFromNumber(intMap.get(TemTemType.ELECTRIC)!!)
    );
    map.set(
      TemTemType.MENTAL,
      this.reactionFromNumber(intMap.get(TemTemType.MENTAL)!!)
    );
    map.set(
      TemTemType.DIGITAL,
      this.reactionFromNumber(intMap.get(TemTemType.DIGITAL)!!)
    );
    map.set(
      TemTemType.MELEE,
      this.reactionFromNumber(intMap.get(TemTemType.MELEE)!!)
    );
    map.set(
      TemTemType.CRYSTAL,
      this.reactionFromNumber(intMap.get(TemTemType.CRYSTAL)!!)
    );
    map.set(
      TemTemType.TOXIC,
      this.reactionFromNumber(intMap.get(TemTemType.TOXIC)!!)
    );

    return map;
  }

  private getReactionIndexesFromType(
    map: Map<TemTemType, number>,
    temTemType: TemTemType
  ) {
    switch (temTemType) {
      case TemTemType.NEUTRAL:
        map.set(TemTemType.MENTAL, map.get(TemTemType.MENTAL)!! - 1);
        break;

      case TemTemType.WIND:
        map.set(TemTemType.WIND, map.get(TemTemType.WIND)!! + 1);
        map.set(TemTemType.EARTH, map.get(TemTemType.EARTH)!! + 1);
        map.set(TemTemType.ELECTRIC, map.get(TemTemType.ELECTRIC)!! - 1);
        break;

      case TemTemType.EARTH:
        map.set(TemTemType.WATER, map.get(TemTemType.WATER)!! - 1);
        map.set(TemTemType.FIRE, map.get(TemTemType.FIRE)!! + 1);
        map.set(TemTemType.NATURE, map.get(TemTemType.NATURE)!! - 1);
        map.set(TemTemType.ELECTRIC, map.get(TemTemType.ELECTRIC)!! + 1);
        map.set(TemTemType.MELEE, map.get(TemTemType.MELEE)!! - 1);
        map.set(TemTemType.CRYSTAL, map.get(TemTemType.CRYSTAL)!! + 1);
        map.set(TemTemType.TOXIC, map.get(TemTemType.TOXIC)!! + 1);
        break;

      case TemTemType.WATER:
        map.set(TemTemType.EARTH, map.get(TemTemType.EARTH)!! + 1);
        map.set(TemTemType.WATER, map.get(TemTemType.WATER)!! + 1);
        map.set(TemTemType.FIRE, map.get(TemTemType.FIRE)!! + 1);
        map.set(TemTemType.NATURE, map.get(TemTemType.NATURE)!! - 1);
        map.set(TemTemType.ELECTRIC, map.get(TemTemType.ELECTRIC)!! - 1);
        map.set(TemTemType.TOXIC, map.get(TemTemType.TOXIC)!! - 1);
        break;

      case TemTemType.FIRE:
        map.set(TemTemType.EARTH, map.get(TemTemType.EARTH)!! - 1);
        map.set(TemTemType.WATER, map.get(TemTemType.WATER)!! - 1);
        map.set(TemTemType.FIRE, map.get(TemTemType.FIRE)!! + 1);
        map.set(TemTemType.NATURE, map.get(TemTemType.NATURE)!! + 1);
        map.set(TemTemType.CRYSTAL, map.get(TemTemType.CRYSTAL)!! + 1);
        break;

      case TemTemType.NATURE:
        map.set(TemTemType.EARTH, map.get(TemTemType.EARTH)!! + 1);
        map.set(TemTemType.WATER, map.get(TemTemType.WATER)!! + 1);
        map.set(TemTemType.FIRE, map.get(TemTemType.FIRE)!! - 1);
        map.set(TemTemType.NATURE, map.get(TemTemType.NATURE)!! + 1);
        map.set(TemTemType.ELECTRIC, map.get(TemTemType.ELECTRIC)!! + 1);
        map.set(TemTemType.TOXIC, map.get(TemTemType.TOXIC)!! - 1);
        break;

      case TemTemType.ELECTRIC:
        map.set(TemTemType.WIND, map.get(TemTemType.WIND)!! + 1);
        map.set(TemTemType.EARTH, map.get(TemTemType.EARTH)!! - 1);
        map.set(TemTemType.ELECTRIC, map.get(TemTemType.ELECTRIC)!! + 1);
        map.set(TemTemType.CRYSTAL, map.get(TemTemType.CRYSTAL)!! - 1);

        break;

      case TemTemType.MENTAL:
        map.set(TemTemType.NEUTRAL, map.get(TemTemType.NEUTRAL)!! + 1);
        map.set(TemTemType.ELECTRIC, map.get(TemTemType.ELECTRIC)!! - 1);
        map.set(TemTemType.DIGITAL, map.get(TemTemType.DIGITAL)!! - 1);
        map.set(TemTemType.MELEE, map.get(TemTemType.MELEE)!! + 1);
        map.set(TemTemType.CRYSTAL, map.get(TemTemType.CRYSTAL)!! - 1);
        break;

      case TemTemType.DIGITAL:
        map.set(TemTemType.WATER, map.get(TemTemType.WATER)!! - 1);
        map.set(TemTemType.ELECTRIC, map.get(TemTemType.ELECTRIC)!! - 1);
        map.set(TemTemType.DIGITAL, map.get(TemTemType.DIGITAL)!! - 1);
        map.set(TemTemType.TOXIC, map.get(TemTemType.TOXIC)!! + 1);
        break;

      case TemTemType.MELEE:
        map.set(TemTemType.MENTAL, map.get(TemTemType.MENTAL)!! - 1);
        map.set(TemTemType.DIGITAL, map.get(TemTemType.DIGITAL)!! - 1);
        map.set(TemTemType.MELEE, map.get(TemTemType.MELEE)!! + 1);
        break;

      case TemTemType.CRYSTAL:
        map.set(TemTemType.EARTH, map.get(TemTemType.EARTH)!! - 1);
        map.set(TemTemType.FIRE, map.get(TemTemType.FIRE)!! - 1);
        map.set(TemTemType.ELECTRIC, map.get(TemTemType.ELECTRIC)!! + 1);
        map.set(TemTemType.MENTAL, map.get(TemTemType.MENTAL)!! + 1);
        map.set(TemTemType.MELEE, map.get(TemTemType.MELEE)!! - 1);
        map.set(TemTemType.TOXIC, map.get(TemTemType.TOXIC)!! + 1);
        break;

      case TemTemType.TOXIC:
        map.set(TemTemType.WIND, map.get(TemTemType.WIND)!! - 1);
        map.set(TemTemType.WATER, map.get(TemTemType.WATER)!! + 1);
        map.set(TemTemType.NATURE, map.get(TemTemType.NATURE)!! + 1);
        map.set(TemTemType.TOXIC, map.get(TemTemType.TOXIC)!! + 1);
        break;
    }
  }

  private reactionFromNumber(value: number): TemTemTypeReaction {
    switch (value) {
      case -2:
        return TemTemTypeReaction.WEAKNESS_4X;

      case -1:
        return TemTemTypeReaction.WEAKNESS_2X;

      case 0:
        return TemTemTypeReaction.NEUTRAL;

      case 1:
        return TemTemTypeReaction.RESISTANT_2X;

      case 2:
        return TemTemTypeReaction.RESISTANT_4X;

      default:
        return TemTemTypeReaction.NEUTRAL;
    }
  }
}

